import { CardTypeGroup, DEFAULT_BAN_STATE_ARR, } from "../../../core/src/main";
import { cloneDeep } from "lodash";
import { computed, defineComponent, reactive, watch, } from "@vue/composition-api";
import VSelect from "vue-select";
import { applicationContainer } from "../inversify.config";
import { APPLICATION_TYPES } from "../types";
const cardDatabase = applicationContainer.get(APPLICATION_TYPES.CardDatabase);
const banlistService = applicationContainer.get(APPLICATION_TYPES.BanlistService);
export default defineComponent({
    props: {
        filter: {
            required: true,
            type: Object,
        },
        showOnly: {
            required: false,
            type: Array,
            default: null,
        },
    },
    components: {
        VSelect,
    },
    model: {
        prop: "filter",
        event: "change",
    },
    setup(props, context) {
        const banStates = DEFAULT_BAN_STATE_ARR;
        const typeGroups = Object.values(CardTypeGroup);
        const reactiveFilter = reactive(cloneDeep(props.filter));
        const sets = computed(() => cardDatabase.getSets());
        const archetypes = computed(() => cardDatabase.getArchetypes());
        const types = computed(() => cardDatabase.getTypes(reactiveFilter.typeGroup));
        const subTypes = computed(() => cardDatabase.getSubTypes(reactiveFilter.typeGroup));
        const attributes = computed(() => cardDatabase.getAttributes());
        const levels = computed(() => cardDatabase.getLevels());
        const linkMarkers = computed(() => cardDatabase.getLinkMarkers());
        const hasBanStates = computed(() => banlistService.hasFormatBanlist(context.root.$store.state.format.active));
        const isMonster = computed(() => reactiveFilter.typeGroup === CardTypeGroup.MONSTER);
        const isFieldVisible = (fieldName) => props.showOnly == null || props.showOnly.includes(fieldName);
        const filterChanged = () => context.emit("change", reactiveFilter);
        watch(() => reactiveFilter.typeGroup, () => {
            reactiveFilter.type = null;
            reactiveFilter.subType = null;
            reactiveFilter.attribute = null;
            reactiveFilter.level = null;
            reactiveFilter.linkMarker = null;
        });
        watch(() => hasBanStates.value, () => {
            reactiveFilter.banState = null;
        });
        return {
            banStates,
            sets,
            archetypes,
            typeGroups,
            types,
            subTypes,
            attributes,
            levels,
            linkMarkers,
            hasBanStates,
            isMonster,
            reactiveFilter,
            filterChanged,
            isFieldVisible,
        };
    },
});
