import { RandomizationStrategy, } from "../../../../core/src/main";
import { applicationContainer } from "../../inversify.config";
import { APPLICATION_TYPES } from "../../types";
import { BModal } from "bootstrap-vue";
import { DECK_REPLACE } from "../../store/modules/deck";
import { defineComponent, ref } from "@vue/composition-api";
import YgoFilter from "../YgoFilter.vue";
import VSelect from "vue-select";
const deckRandomizationService = applicationContainer.get(APPLICATION_TYPES.DeckRandomizationService);
export default defineComponent({
    components: {
        YgoFilter,
        VSelect,
        BModal,
    },
    props: {},
    setup(props, context) {
        const strategies = Object.values(RandomizationStrategy);
        const strategy = ref(RandomizationStrategy.ARCHETYPE_2);
        const filter = ref({
            name: null,
            typeGroup: null,
            type: null,
            subType: null,
            attribute: null,
            level: null,
            linkMarker: null,
            archetype: null,
            format: null,
            banState: null,
            sets: [],
        });
        const modal = ref(null);
        const randomize = () => {
            const randomizedDeck = deckRandomizationService.randomize(strategy.value, filter.value);
            context.root.$store.commit(DECK_REPLACE, { deck: randomizedDeck });
        };
        const showModal = () => {
            modal.value.show();
        };
        return { strategy, strategies, filter, modal, showModal, randomize };
    },
});
